
* {
    box-sizing: border-box;
    -webkit-box-sizing: border-box;
}
*::-webkit-scrollbar {
    width: 0;
    height: 0;
    background-color: #fff;
}
html,
  body {
    width: 100%;
    height: 100vh;
    overflow: hidden;
    font-size: $font_size_12;
    font-family: Arial, 'Microsoft YaHei', SimHei, sans-serif, "Heiti SC";
    margin: 0;
    padding: 0;
    font-weight: 500;
    background-color: $gray;
}
input,
  textarea,
  button,
  pre {
    border: 0;
    outline: 0;
    font-family: Arial, 'Microsoft YaHei', SimHei, sans-serif;
}
a,
  button,
  input {
    -webkit-appearance: none;
    outline: 0;
}
a {
    text-decoration: none;
}
ul {
    list-style: none;
}
.fr {
    float: right;
}
.fl {
    float: left;
}




















